@import "./base.scss";
.landing{
  position: relative;
  width: 100%;
  height: 100vh;
  background: $bg-landing;
  color: $white-color;
  overflow: hidden;
.header{
  position: absolute;
  top: 1rem;
  left: 50%;
  transform: translateX(-50%);
  &__wrap {
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 60vw;

  }  &__left {
       display: flex;
       align-items: center;
       gap: 2rem;
     & li{
       font-size: 18px;
       font-weight: 200;
     }
       }
}
}

